p{
  display: flex;
  justify-content: center;
  font-size: 30px;
}

table {
  width: 90%;
  table-layout: fixed;
  margin: auto;
  font-weight: bolder;
}

table, thead, td {
  border: 1px solid black;
  border-collapse: collapse;
}

table thead tr {
  text-align: center;
  background: rgb(58, 56, 56);
  color: rgb(253, 251, 251);
  border: black 1px;
}

table tbody tr {
  text-align: center;
  background: rgb(179, 179, 179);
  color: black;
}

button {
  background-color: rgb(58, 56, 56);
  padding: 1rem 1rem;
  color: white;
  width: 100%;
}

button:hover {
  background-color: rgb(73, 73, 73);
  color: white;
}
