form{
    display: flex;
    flex-direction: column;
    width: 80%;
    margin: auto;
    padding-top: 0.5rem;
}

label{
    padding-top: 0.5rem;
    font-weight: bolder;

}

input[type=text]{
    height: 2rem;
}

input[type=submit] {
    background-color: rgb(58, 56, 56);
    padding: 1rem 1rem;
    color: white;
    width: 20%;
    margin:auto;
    margin-top: 1rem;
    font-weight: bolder;
    font-size: 20px;
}

#submit:hover {
    background-color: rgb(73, 73, 73);
    color: white;
  }

